@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
*{font-family:'Inter', sans-serif}html{min-height:100%;box-sizing:border-box}body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

.App{display:grid;min-height:100vh;background-color:#eff1f6}.app-header{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;background-color:#273553;-webkit-align-items:center;align-items:center;padding:0.5rem 1rem;min-height:2rem;color:white}@media screen and (max-width: 480px){.app-header{-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;text-align:center;-webkit-flex-direction:column;flex-direction:column}}.app-header a{text-decoration:none;color:white;border:solid 1px white;border-radius:0.3rem;padding:1rem}.app-header a:hover{background-color:#e3203b;font-weight:bold;border:none;transition:0.2s ease-in-out}.app-content{padding:1em}

.react-tabs{-webkit-tap-highlight-color:transparent}.react-tabs__tab-list{padding:0}.react-tabs__tab{display:inline-block;border:1px solid transparent;border-bottom:none;bottom:-1px;position:relative;list-style:none;padding:6px 1rem;margin-right:0.4rem;cursor:pointer;background-color:#374b76}.react-tabs__tab--selected{background:#fff;border-color:#aaa;color:black}.react-tabs__tab--disabled{color:GrayText;cursor:default}.react-tabs__tab:focus:after{content:"";position:absolute;height:5px;left:-4px;right:-4px;bottom:-5px;background:#fff}.react-tabs__tab-panel{display:none}.react-tabs__tab-panel--selected{display:block}

.QuestionPage{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-row-gap:1rem;row-gap:1rem}.question-page-section{padding:1rem;border:solid 1px black;background-color:#fff;border-radius:0.3rem}.question-title-area{font-size:1rem;padding:0 1rem;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;margin-bottom:1rem}@media screen and (max-width: 600px){.question-title-area{-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:column;flex-direction:column}}.question{font-weight:500}.change-question{padding:1rem;background-color:#e3203b;color:white;font-weight:bold;border:none;border-radius:0.3rem}.change-question:hover{cursor:pointer;background-color:black;color:white;transition:0.2s ease-in-out}@media screen and (max-width: 600px){.change-question{width:100%;margin-bottom:1rem}}.question-area{display:grid;grid-template-columns:2fr 1fr;grid-auto-rows:minmax(700px, auto);grid-gap:1rem;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}@media screen and (max-width: 768px){.question-area{grid-template-columns:auto}}.question-area-video{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.recording-option-container{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap}.recording-option{padding:0.5em;width:50%;text-overflow:wrap;font-weight:500;font-size:1em}.recording-option:hover{cursor:pointer}@media screen and (max-width: 500px){.recording-option-container{-webkit-flex-direction:column;flex-direction:column}.recording-option{width:100%}}.question-area-notes{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.question-area-notes-input{resize:none;padding:1rem;height:80%;font-weight:bold}.current-question{font-size:1.4rem;font-weight:bold}.question-selection-button{border:none;padding:0.5rem 1rem}.question-selection-button:hover{cursor:pointer;transition:ease-in-out 0.2s}.question-table{width:100%}.question-table td{padding:0.5rem;border:solid 1px white}.question-table-minor{text-align:center}

.RMM__close-button{position:absolute;top:20px;right:20px;cursor:pointer;height:36px;width:36px;background:rgba(0,0,0,0.4);border-radius:50%;border:none;text-indent:100%;overflow:hidden;color:transparent;white-space:nowrap;transition:opacity 0.365s, -webkit-transform 0.365s;transition:transform 0.365s, opacity 0.365s;transition:transform 0.365s, opacity 0.365s, -webkit-transform 0.365s;z-index:10002;-webkit-transform:scale(0);transform:scale(0);opacity:0}.RMM__close-button::before,.RMM__close-button::after{content:"";position:absolute;top:50%;left:50%;height:2px;width:24px;background:#fff}.RMM__close-button::before{-webkit-transform:translate(-50%) rotate(45deg);transform:translate(-50%) rotate(45deg)}.RMM__close-button::after{-webkit-transform:translate(-50%) rotate(-45deg);transform:translate(-50%) rotate(-45deg)}.RMM__close-button:hover{background:rgba(0,0,0,0.6)}.RMM__close-button--is-active{-webkit-transform:scale(1);transform:scale(1);opacity:1}.RMM__container{position:fixed;top:0;left:0;height:100%;overflow-y:scroll;width:100%;pointer-events:none;opacity:0;visibility:hidden;z-index:10000}.RMM__container--is-active{pointer-events:initial;opacity:1;visibility:visible}.RMM__body{background-color:#273553;color:white;position:relative;height:100%;overflow-y:scroll;padding:1rem;z-index:10001;opacity:0;transition:opacity 0.2s}.RMM__body--is-active{opacity:1;overflow-y:scroll}.RMM__body--no-padding{padding:0}.RMM__placeholder{position:fixed;top:0;left:0;transition-property:opacity, -webkit-transform;transition-property:transform, opacity;transition-property:transform, opacity, -webkit-transform;transition-duration:0.365s}

.footer{background-color:#0d61f2;text-align:center;margin:0}.footer a{text-decoration:none;color:white}

